import { Iterator } from './Iterator';

class CursorBasedIterator extends Iterator {
  private readonly _nextCursor;
  private readonly _prevCursor;
  readonly cursors;

  constructor({
    items,
    originQuery,
    fetchNextPage,
    fetchPrevPage,
    limit,
    nextCursor,
    prevCursor,
  }) {
    super({ items, originQuery, fetchNextPage, fetchPrevPage, limit });
    this._nextCursor = nextCursor;
    this._prevCursor = prevCursor;
    this.cursors = {
      next: nextCursor,
      prev: prevCursor,
    };
  }

  hasNext() {
    return !!this._nextCursor;
  }

  hasPrev() {
    return !!this._prevCursor;
  }
}

export { CursorBasedIterator };
